<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
    </div>
    <div class="operating">
        <el-form :inline="true" :model="searchForm" :rules="searchRules" ref="searchForm" class="demo-form-inline" size="mini">
            <el-form-item label="" prop="userName">
                <el-input v-model="searchForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="linkmanName">
                <el-input v-model="searchForm.linkmanName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="linkmanPhone">
                <el-input v-model="searchForm.linkmanPhone"></el-input>
            </el-form-item>
            <el-form-item label="" prop="linkmanEmail">
                <el-input v-model="searchForm.linkmanEmail"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="searchSubmit('searchForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
    <div class="table">
        <div class="table-btn">
            <el-button size="mini" type="success" @click="handleForm('add')"></el-button>
        </div>
      <el-table
        v-loading="loading"
        :data="tableData"
        size="small"
        border
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column align="center" prop="userId" label="" width="100" fixed></el-table-column>
        <el-table-column align="center" prop="userName" label="" fixed></el-table-column>
        <el-table-column align="center" prop="linkmanName" label=""></el-table-column>
        <el-table-column align="center" prop="linkmanPhone" label="" width="120"></el-table-column>
        <el-table-column align="center" prop="merchantName" label=""></el-table-column>
        <el-table-column align="center" prop="linkmanEmail" label="" width="150"></el-table-column>
        <el-table-column align="center" prop="status" label=""></el-table-column>
        <el-table-column align="center" prop="createTime" label="" width="160"></el-table-column>
        <el-table-column align="center" prop="operate" label="" width="100" fixed="right">
          <template slot-scope="scope">
            <el-button v-if="scope.row.operate.update" @click="handleForm(scope.row)" type="warning" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination background layout="prev, pager, next" 
        v-if="page.totalPages > 1"
        @current-change="handleCurrentChange"
        :current-page="page.page"
        :page-size="page.rows" 
        :total="page.total"
      />
    </div>
    <el-dialog
      v-show="addShow"
      :visible.sync="addShow"
      :title="form.formTitle"
      width="30%"
      :before-close="addClose"
      center
      >
      <div>
        <el-form ref="form" :model="form" :rules="rules" size="small" label-width="105px">
          <el-form-item label="">
            <el-input v-model="form.merchantName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleId">
            <el-select v-model="form.roleId" placeholder="">
                <el-option v-for="(role, index) in roleList" :key="index" :label="role.roleName" :value="role.roleId"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="linkmanName">
            <el-input v-model="form.linkmanName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="userName">
            <el-input v-model="form.userName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="nickName">
            <el-input v-model="form.nickName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="form.password" type="password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass">
            <el-input v-model="form.checkPass" type="password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="linkmanPhone">
            <el-input v-model="form.linkmanPhone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="linkmanEmail">
            <el-input v-model="form.linkmanEmail" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="QQ" prop="qq">
            <el-input v-model="form.qq" placeholder="QQ"></el-input>
          </el-form-item>
          <el-form-item label="" prop="wechat">
            <el-input v-model="form.wechat" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
              <el-select v-model="form.status" placeholder="">
                  <el-option label="" value="VALID"></el-option>
                  <el-option label="" value="INVALID"></el-option>
                  <!-- <el-option label="" value="2"></el-option> -->
              </el-select>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addShow = false"> </el-button>
        <el-button size="small" type="primary" @click="formSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import { axiosMixin, listMixin, validMixin } from "static/js/mixin.js";
import _user from "service/user-service.js";
export default {
  mixins: [axiosMixin, listMixin, validMixin],
  data() {
    return {
      searchForm: {
        userName: "",
        linkmanName: "",
        linkmanPhone: "",
        linkmanEmail: ""
      },
      searchRules: {
        userName: [
          { required: false, message: "", trigger: "blur" }
        ],
        nickName: [{ required: false, message: "", trigger: "blur" }],
        linkmanName: [
          { required: false, message: "", trigger: "blur" }
        ],
        linkmanPhone: [
          { required: false, message: "", trigger: "blur" }
        ],
        linkmanEmail: [
          { required: false, message: "", trigger: "blur" }
        ]
      },
      rules: {
        roleId: [{ required: true, message: "", trigger: "blur" }],
        linkmanName: [
          { required: true, message: "", trigger: "blur" }
        ],
        userName: [
          { required: true, message: "", trigger: "blur" }
        ],
        nickName: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.checkPass, trigger: "blur" }
        ],
        checkPass: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.checkPass2, trigger: "blur" }
        ],
        linkmanPhone: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.checkPhone, trigger: "blur" }
        ],
        linkmanEmail: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: "blur"
          }
        ],
        qq: [
          { required: true, message: "QQ", trigger: "blur" },
          { validator: this.checkQQ, trigger: "blur" }
        ],
        wechat: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.checkWechat, trigger: "blur" }
        ],
        status: [{ required: true, message: "", trigger: "blur" }]
      },
      form: {
        formTitle: ""
      },
      roleList: []
    };
  },
  created() {
    this._renderTableDate();
    this.getRoleList();
  },
  methods: {
    // 
    _renderTableDate(data) {
      const _this = this;
      _this.loading = true;
      _user.userList(data).then(res => {
        _this.renderTableDate(res, item => {
          return {
            userId: item.userId,
            userName: item.userName,
            nickName: item.nickName,
            merchantId: item.merchantId,
            merchantName: item.merchantName,
            linkmanName: item.linkmanName,
            linkmanPhone: item.linkmanPhone,
            linkmanEmail: item.linkmanEmail,
            roleId: item.roleId,
            wechat: item.wechat,
            qq: item.qq,
            status: item.status === "VALID" ? "" : "",
            createTime: item.createTime,
            operate: {
              add: true,
              update: true
            }
          };
        });
      });
    },
    // 
    searchSubmit(searchForm) {
      const _this = this;
      this.$refs[searchForm].validate(valid => {
        if (valid) {
          _this._renderTableDate(_this.$refs[searchForm].model);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // roleList
    getRoleList() {
      const _this = this;
      _user.roleList().then(res => {
        _this.filterAxios(res, res => {
          console.log("roleList:", res);
          this.roleList = res.list;
        });
      });
    },
    // 
    getUserDetail(userId) {
      _user.getUserDetail(userId).then(res => {
        this.filterAxios(res, res => {
          this.form = {
            ...this.form,
            ...res
          };
          console.log('this.form', this.form);
        });
      });
    },
    //  
    handleForm(row) {
      if (row instanceof Object) {
        this.getUserDetail(row.userId);
        this.form = {
          ...this.form,
          formTitle: ""
        };
        console.log('update', this.form);
      } else {
        this.form = {
          formTitle: "",
          merchantId: this.userInfo.merchantId,
          merchantName: this.userInfo.merchantName
        };
      }
      this.addShow = true;
    },
    formSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          delete this.form.checkPass; // 
          if (this.form.formTitle === "") {
            delete this.form.formTitle
            _user.addUser(this.form).then(res => {
              this.formatResult(res, "");
            });
          } else {
            delete this.form.merchantName;
            _user.updateUser(this.form.userId, this.form).then(res => {
              this.formatResult(res, "");
            });
          }
        } else {
          return false;
        }
      });
    },
    formClose() {
      this.addShow = false;
      this.form = {};
      this.$refs["form"] && this.$refs["form"].resetFields();
    },
    formatResult(res, msg) {
      const _this = this;
      _this.formClose();
      _this.filterAxios(res, res => {
        _this.successTips(msg);
        _this._renderTableDate();
      });
    }
  }
};
</script>


