<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
    </div>
    <div class="table">
        <div class="table-btn">
            <el-button size="mini" type="success" @click="addShow = true"></el-button>
        </div>
      <el-table
        :data="tableData"
        size="small"
        border
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column align="center" prop="id" label=""></el-table-column>
        <el-table-column align="center" prop="code" label=""></el-table-column>
        <el-table-column align="center" prop="remark" label=""></el-table-column>
        <el-table-column align="center" prop="status" label=""></el-table-column>
        <el-table-column align="center" prop="createTime" label=""></el-table-column>
        <el-table-column align="center" prop="updateTime" label=""></el-table-column>
        <el-table-column align="center" prop="operate" label="" width="150">
          <template slot-scope="scope">
            <el-button v-if="scope.row.operate.update" @click="handleUpdate(scope.row)" type="warning" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      v-show="addShow"
      :visible.sync="addShow"
      title=""
      width="30%"
      :before-close="addClose"
      center
      >
      <div>
        <el-form ref="addForm" :model="addForm" size="small" label-width="105px">
          <el-form-item label="">
            <el-input v-model="addForm.code" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addForm.remark" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
              <el-select v-model="addForm.status" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="0"></el-option>
              </el-select>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addShow = false"> </el-button>
        <el-button size="small" type="primary" @click="addSubmit"> </el-button>
      </span>
    </el-dialog>
    <el-dialog
      v-show="updateShow"
      :visible.sync="updateShow"
      title=""
      width="30%"
      :before-close="updateClose"
      center
      >
      <div>
        <el-form ref="updateForm" :model="updateForm" size="small" label-width="105px">
          <el-form-item label="">
            <el-input v-model="updateForm.code" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="updateForm.remark" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
              <el-select v-model="updateForm.status" placeholder="">
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="0"></el-option>
              </el-select>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="updateShow = false"> </el-button>
        <el-button size="small" type="primary" @click="updateSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
export default {
  data() {
    return {
      addShow: false,
      addForm: {},
      updateShow: false,
      updateForm: {},
      tableData: [
        {
          id: "1",
          code: "code",
          remark: "remark",
          status: 1,
          createTime: "2018-04-30",
          updateTime: "2018-04-30",
          operate: {
            update: true
          }
        },
        {
          id: "2",
          code: "code",
          remark: "remark",
          status: 1,
          createTime: "2018-04-30",
          updateTime: "2018-04-30",
          operate: {
            update: true
          }
        },
        {
          id: "3",
          code: "code",
          remark: "remark",
          status: 1,
          createTime: "2018-04-30",
          updateTime: "2018-04-30",
          operate: {
            update: true
          }
        }
      ]
    };
  },
  methods: {
    addClose() {
      this.addShow = false;
      this.$refs["addForm"].resetFields();
    },
    addSubmit() {
      console.log("addSubmit!");
    },
    updateClose() {
      this.updateShow = false;
      this.$refs["updateForm"].resetFields();
    },
    updateSubmit() {
      console.log("updateSubmit!");
    },
    handleAdd(row) {
      let id = row.id;
      this.addShow = true;
      this.addForm = {};
      console.log("handleAdd!", row, id);
    },
    handleUpdate(row) {
      let id = row.id;
      this.updateShow = true;
      this.updateForm = {
        code: row.code,
        remark: row.remark,
        status: row.status
      };
      console.log("handleUpdate!", row, id);
    }
  }
};
</script>


