import { _axios } from "static/js/axios.js";
const _common = {
  /**
   * 
   * CARD_TYPE-
   * CHANNEL_TYPE-
   * MERCHANT_TYPE-
   * PAY_TYPE-
   * PRODUCT_CODE-
   * PROD_RATE_TYPE-
   * RECORD_STATUS-
   * TRADE_STATUS-
   * TRADE_TYPE-
   */
  getDictionaryList: function (parentCode) {
    return _axios('/sys/dic/' + parentCode + '/list');
  }
}

export default _common;



