<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
    </div>
    <!--  -->
    <div class="detail-input">
        <el-form :inline="true" :model="searchForm" ref="searchForm" class="demo-form-inline" size="mini" label-width="100px">
            <el-form-item label="">
                <el-input v-model="searchForm.merchantId" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="searchForm.merchantName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="searchForm.status" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value="VALID"></el-option>
                    <el-option label="" value="INVALID"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item class="btn-item">
                <el-button type="primary" @click="searchSubmit('searchForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
    <div class="table">
        <div class="table-btn">
            <el-button size="mini" type="success" @click="handleStore()"></el-button>
        </div>
      <el-table
        v-loading="loading"
        :data="tableData"
        size="small"
        border
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column align="center" prop="no" label="" fixed></el-table-column>
        <el-table-column align="center" prop="merchantId" label="" fixed width="120"></el-table-column>
        <el-table-column align="center" prop="merchantName" label="" fixed width="120"></el-table-column>
        <el-table-column align="center" prop="merchantType" label=""></el-table-column>
        <el-table-column align="center" prop="linkmanName" label="" width="100"></el-table-column>
        <el-table-column align="center" prop="linkmanPhone" label="" width="100"></el-table-column>
        <el-table-column align="center" prop="linkmanEmail" label="" width="150"></el-table-column>
        <el-table-column align="center" prop="status" label=""></el-table-column>
        <el-table-column align="center" prop="createTime" label="" width="150"></el-table-column>
        <el-table-column align="center" prop="agentMerchantName" label="" width="120"></el-table-column>
        <el-table-column align="center" prop="amount" label=""></el-table-column>
        <el-table-column align="center" prop="freezingAmount" label=""></el-table-column>
        <el-table-column align="center" prop="operate" label="" fixed="right" width="480">
          <template slot-scope="scope">
            <el-button @click="goProductList(scope.row)" type="warning" size="mini"></el-button>
            <el-button  v-if="scope.row.operate.add" @click="handleStore(scope.row, 'add')" type="success" size="mini"></el-button>
            <el-button @click="handleStore(scope.row, 'update')" type="warning" size="mini"></el-button>
            <el-button @click="handleAddUser(scope.row)" type="success" size="mini"></el-button>
            <el-button @click="handleAppInfo(scope.row)" type="warning" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination background layout="prev, pager, next" 
        v-if="page.totalPages > 1"
        @current-change="handleCurrentChange"
        :current-page="page.page"
        :page-size="page.rows" 
        :total="page.total"
      />
    </div>
    <!--  -->
    <el-dialog
      v-show="addShow"
      :visible.sync="addShow"
      :title="form.title"
      width="75%"
      :before-close="addClose"
      center
      >
      <div class="detail-input">
        <el-form :inline="true" ref="form" :model="form" :rules="rules" size="small" label-width="105px">
          <div class="el-form-line">
            <el-form-item label="" prop="merchantId">
              <el-input v-model="form.merchantId" placeholder="" :disabled="form.title===''"></el-input>
            </el-form-item>
            <el-form-item label="" prop="merchantName">
              <el-input v-model="form.merchantName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="merchantType">
              <el-select v-model="form.merchantType" placeholder="">
                  <el-option label="" value="COMMON"></el-option>
                  <el-option label="" value="AGENT"></el-option>
              </el-select>
            </el-form-item>
          </div>
          <div class="el-form-line">
            <el-form-item label="" prop="linkmanName">
              <el-input v-model="form.linkmanName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="linkmanPhone">
              <el-input v-model="form.linkmanPhone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="linkmanEmail">
              <el-input v-model="form.linkmanEmail" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div class="el-form-line">
            <el-form-item label="" prop="merchantAgentName">
              <el-input v-model="form.merchantAgentName" :disabled="true"></el-input>
            </el-form-item>
            <el-form-item label="" prop="cardType">
              <el-select v-model="form.cardType" placeholder="">
                  <el-option label="" value="PSN_DEBIT"></el-option>
                  <el-option label="" value="PUB"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="bankCardNo">
              <el-input v-model="form.bankCardNo" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div class="el-form-line">
            <el-form-item label="" prop="accountName">
              <el-input v-model="form.accountName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="bankName">
              <el-input v-model="form.bankName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="branchNo">
              <el-input v-model="form.branchNo" placeholder=""></el-input>
            </el-form-item>
          </div>
          <div class="el-form-line">
            <el-form-item label="" prop="payPassword">
              <el-input v-model="form.payPassword" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="md5Key">
              <el-input v-model="form.md5Key" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="form.status" placeholder="">
                  <el-option label="" value="VALID"></el-option>
                  <el-option label="" value="INVALID"></el-option>
              </el-select>
            </el-form-item>
          </div>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addShow = false"> </el-button>
        <el-button size="small" type="primary" @click="addSubmit"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      v-show="addUserShow"
      :visible.sync="addUserShow"
      title=""
      width="30%"
      :before-close="addUserClose"
      center
      >
      <div>
        <el-form ref="addUserForm" :model="addUserForm" :rules="addUserRules" size="small" label-width="105px">
          <el-form-item label="">
            <el-input v-model="addUserForm.merchantName" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleId">
            <el-select v-model="addUserForm.roleId" placeholder="">
                <el-option v-for="(role, index) in roleList" :key="index" :label="role.roleName" :value="role.roleId"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="linkmanName">
            <el-input v-model="addUserForm.linkmanName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="userName">
            <el-input v-model="addUserForm.userName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="nickName">
            <el-input v-model="addUserForm.nickName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addUserForm.password" type="password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass">
            <el-input v-model="addUserForm.checkPass" type="password" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="linkmanPhone">
            <el-input v-model="addUserForm.linkmanPhone" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="linkmanEmail">
            <el-input v-model="addUserForm.linkmanEmail" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="QQ" prop="qq">
            <el-input v-model="addUserForm.qq" placeholder="QQ"></el-input>
          </el-form-item>
          <el-form-item label="" prop="wechat">
            <el-input v-model="addUserForm.wechat" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
              <el-select v-model="addUserForm.status" placeholder="">
                  <el-option label="" value="VALID"></el-option>
                  <el-option label="" value="INVALID"></el-option>
                  <!-- <el-option label="" value="2"></el-option> -->
              </el-select>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addUserShow = false"> </el-button>
        <el-button size="small" type="primary" @click="addUserSubmit"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      v-show="updateAppInfoShow"
      :visible.sync="updateAppInfoShow"
      title=""
      width="30%"
      :before-close="updateAppInfoClose"
      center
      >
      <div>
        <el-form ref="appInfoForm" :model="appInfoForm" :rules="appInfoRules" size="small" label-width="105px">
          <el-form-item label="APPID">
            <el-input v-model="appInfoForm.appId" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="appInfoForm.merchantId" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="md5">
            <el-input type="textarea" v-model="appInfoForm.md5Key"></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
              <el-select v-model="appInfoForm.status" placeholder="">
                  <el-option label="" value="VALID"></el-option>
                  <el-option label="" value="INVALID"></el-option>
              </el-select>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="updateAppInfoShow = false"> </el-button>
        <el-button size="small" type="primary" @click="appSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import { axiosMixin, listMixin } from "static/js/mixin.js";
import { formatMoney } from "static/js/format.js";
import _store from "service/store-service.js";
import _user from "service/user-service.js";
export default {
  mixins: [axiosMixin, listMixin],
  data() {
    const checkBranchNo = (rule, value, callback) => {
      if (this.form.cardType === "PUB" && value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const checkPayPassword = (rule, value, callback) => {
      if (this.form.title === "" && value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const checkMd5Key = (rule, value, callback) => {
      if (this.form.title === "" && value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.addUserForm.checkPass !== "") {
          this.$refs.addUserForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.addUserForm.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      addUserShow: false,
      updateAppInfoShow: false,
      storeAppInfo: {},
      searchForm: {
        merchantId: "",
        merchantName: "",
        status: ""
      },
      form: {},
      rules: {
        merchantId: [
          { required: true, message: "", trigger: "blur" }
        ],
        merchantName: [
          { required: true, message: "", trigger: "blur" }
        ],
        merchantType: [
          { required: true, message: "", trigger: "blur" }
        ],
        linkmanName: [
          { required: true, message: "", trigger: "blur" }
        ],
        linkmanPhone: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.checkPhone, trigger: "blur" }
        ],
        linkmanEmail: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: "blur"
          }
        ],
        agentMerchantName: [
          { required: false, message: "", trigger: "blur" }
        ],
        cardType: [
          { required: true, message: "", trigger: "blur" }
        ],
        bankCardNo: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 16,
            max: 19,
            message: "1619",
            trigger: "blur"
          }
        ],
        accountName: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.checkChinese, trigger: "blur" }
        ],
        bankName: [
          { required: true, message: "", trigger: "blur" }
        ],
        // 
        branchNo: [{ validator: checkBranchNo, trigger: "blur" }],
        payPassword: [
          { validator: checkPayPassword, trigger: "blur" },
          {
            min: 6,
            max: 16,
            message: "616",
            trigger: "blur"
          }
        ],
        md5Key: [{ validator: checkMd5Key, trigger: "blur" }],
        status: [{ required: true, message: "", trigger: "blur" }]
      },
      appInfoForm: {},
      appInfoRules: {},
      addUserForm: {},
      addUserRules: {
        roleId: [{ required: true, message: "", trigger: "blur" }],
        linkmanName: [
          { required: true, message: "", trigger: "blur" }
        ],
        userName: [
          { required: true, message: "", trigger: "blur" }
        ],
        nickName: [{ required: true, message: "", trigger: "blur" }],
        password: [
          { required: true, message: "", trigger: "blur" },
          { validator: validatePass, trigger: "blur" }
        ],
        checkPass: [
          { required: true, message: "", trigger: "blur" },
          { validator: validatePass2, trigger: "blur" }
        ],
        linkmanPhone: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.checkPhone, trigger: "blur" }
        ],
        linkmanEmail: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: "blur"
          }
        ],
        qq: [
          { required: true, message: "QQ", trigger: "blur" },
          { validator: this.checkQQ, trigger: "blur" }
        ],
        wechat: [
          { required: true, message: "", trigger: "blur" },
          { validator: this.checkWechat, trigger: "blur" }
        ],
        status: [{ required: true, message: "", trigger: "blur" }]
      },
      roleList: []
    };
  },
  created() {
    this._renderTableDate();
  },
  methods: {
    // 
    _renderTableDate(data) {
      const _this = this;
      _this.loading = true;
      _store.getStoreList(data).then(
        res => {
          _this.renderTableDate(res, (item, index) => {
            return {
              no: index + 1,
              id: item.id,
              merchantId: item.merchantId,
              merchantName: item.merchantName,
              linkmanName: item.linkmanName,
              linkmanPhone: item.linkmanPhone,
              linkmanEmail: item.linkmanEmail,
              merchantType:
                item.merchantType === "AGENT" ? "" : "",
              merchantAgentId: item.merchantAgentId,
              agentMerchantName: item.agentMerchantName,
              amount: formatMoney(item.amount),
              freezingAmount: formatMoney(item.freezingAmount),
              status: item.status === "VALID" ? "" : "",
              createTime: item.createTime,
              operate: {
                add: item.merchantType === "AGENT",
                update: true
              }
            };
          });
        },
        err => {
          console.log("err", err);
        }
      );
    },
    // 
    searchSubmit(searchForm) {
      const _this = this;
      _this.$refs[searchForm].validate(valid => {
        if (valid) {
          let username = this.$refs[searchForm].model.feature;
          _this._renderTableDate(_this.searchForm);
          console.log("submit!", username);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 
    handleStore(row, type) {
      const _this = this;
      _this.addShow = true;
      if (row instanceof Object) {
        if (type && type === "update") {
          // 
          _this.getStoreInfo(row.id);
        } else {
          _this.form = {
            title: "",
            merchantAgentId: row.merchantId,
            merchantAgentName: row.merchantName
          };
        }
      } else {
        _this.form = {
          title: ""
        };
      }
      console.log("this.form", this.form);
    },
    // 
    getStoreInfo(id) {
      const _this = this;
      _store.getStoreDetailInfo(id).then(res => {
        _this.filterAxios(res, res => {
          _this.form = {
            title: "",
            merchantId: res.merchantId,
            merchantName: res.merchantName,
            merchantType: res.merchantType,
            linkmanName: res.linkmanName,
            linkmanPhone: res.linkmanPhone,
            linkmanEmail: res.linkmanEmail,
            merchantAgentId: res.merchantAgentId + "",
            merchantAgentName: res.merchantAgentName,
            cardType: res.cardType,
            bankCardNo: res.bankCardNo,
            accountName: res.accountName,
            bankName: res.bankName,
            branchNo: res.branchNo,
            payPassword: res.payPassword,
            md5Key: res.md5Key,
            status: res.status
          };
        });
      });
    },
    addSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.form.title === "") {
            delete this.form.title;
            this.form.agentMerchantName && delete this.form.agentMerchantName;
            _store.addStroreUser(this.form).then(res => {
              this.formatResult(res, "");
            });
          } else {
            // 
            delete this.form.title;
            this.form.agentMerchantName && delete this.form.agentMerchantName;
            _store.updateStroreUser(this.form.id, this.form).then(res => {
              this.formatResult(res, "");
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    formClose() {
      this.addShow = false;
      this.$refs["form"].resetFields();
      this.form = {};
    },
    formatResult(res, msg) {
      this.formClose();
      this.filterAxios(res, res => {
        this.successTips(msg);
        this._renderTableDate();
      });
    },
    // 
    handleAppInfo(row) {
      this.appInfoForm = {};
      this.getAppInfo(row.merchantId);
      this.updateAppInfoShow = true;
    },
    updateAppInfoClose() {
      this.updateAppInfoShow = false;
      this.$refs["appInfoForm"].resetFields();
      this.appInfoForm = {};
    },
    appSubmit() {
      _store
        .updateAppInfo(this.appInfoForm.merchantId, this.appInfoForm)
        .then(res => {
          this.filterAxios(res, res => {
            this.successTips("");
            this.updateAppInfoClose();
          });
        });
    },
    // 
    getAppInfo(merchantId) {
      _store.getStoreAppInfo(merchantId).then(res => {
        this.filterAxios(res, res => {
          this.storeAppInfo = res;
          this.appInfoForm = {
            merchantId: this.storeAppInfo.merchantId,
            appId: this.storeAppInfo.appId,
            id: this.storeAppInfo.id,
            md5Key: this.storeAppInfo.md5Key,
            status: this.storeAppInfo.status
          };
        });
      });
    },
    // 
    goProductList(row) {
      this.$router.push({
        path: "/product_list",
        query: {
          merchantId: row.merchantId,
          merchantName: row.merchantName,
          agentMerchantName: row.agentMerchantName
        }
      });
    },
    // 
    handleAddUser(row) {
      const _this = this;
      _this.addUserForm = {
        merchantId: row.merchantId,
        merchantName: row.merchantName
      };
      this.roleList.length <= 0 && this.getRoleList();
      _this.addUserShow = true;
    },
    addUserClose() {
      this.addUserShow = false;
      this.addUserForm = {};
      this.$refs["addUserForm"] && this.$refs["addUserForm"].resetFields();
    },
    addUserSubmit() {
      this.$refs.addUserForm.validate(valid => {
        if (valid) {
          console.log("addUserForm", this.addUserForm);
          delete this.form.checkPass; // 
          _user.addUser(this.addUserForm).then(res => {
            this.filterAxios(res, res => {
              this.successTips("");
              this.addUserClose();
              this._renderTableDate();
            });
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // roleList
    getRoleList() {
      const _this = this;
      _user.roleList().then(res => {
        _this.filterAxios(res, res => {
          this.roleList = res.list;
        });
      });
    }
  }
};
</script>


