<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
    </div>
    <div class="operating">
        <el-form :inline="true" :model="searchForm" :rules="searchRules" ref="searchForm" class="demo-form-inline" size="mini">
            <el-form-item label="" prop="channelCode">
                <el-input v-model="searchForm.channelCode"></el-input>
            </el-form-item>
            <el-form-item label="" prop="channelName">
                <el-input v-model="searchForm.channelName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="productCode">
                <el-select v-model="searchForm.productCode" placeholder="">
                    <el-option label="" value="" />
                    <el-option v-for="(item, index) in productCodeList" :key="index" :label="item.dicName" :value="item.dicCode"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="searchSubmit('searchForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
    <div class="table">
        <div class="table-btn">
            <el-button size="mini" type="success" @click="handleForm()"></el-button>
        </div>
      <el-table
        :data="tableData"
        size="small"
        border
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column align="center" prop="no" label="" fixed></el-table-column>
        <el-table-column align="center" prop="channelCode" label="" fixed></el-table-column>
        <el-table-column align="center" prop="channelName" label="" fixed></el-table-column>
        <el-table-column align="center" prop="channelTypeDesc" label="" width="100"></el-table-column>
        <el-table-column align="center" prop="productTypeDesc" label="" width="100"></el-table-column>
        <el-table-column align="center" prop="gatewayGroup" label=""></el-table-column>
        <el-table-column align="center" prop="status" label=""></el-table-column>
        <el-table-column align="center" prop="sort" label=""></el-table-column>
        <el-table-column align="center" prop="createTime" label="" width="150"></el-table-column>
        <el-table-column align="center" prop="updateTime" label="" width="150"></el-table-column>
        <el-table-column align="center" label="" width="300" fixed="right">
          <template slot-scope="scope">
            <el-button @click="handleForm(scope.row, '')" type="warning" size="mini"></el-button>
            <el-button @click="handleSupportBankList(scope.row.channelId, scope.row.channelName)" type="warning" size="mini"></el-button>
            <el-button v-if="scope.row.operate.update" @click="handleForm(scope.row, '')" type="warning" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination background layout="prev, pager, next" 
        v-if="page.totalPages > 1"
        @current-change="handleCurrentChange"
        :current-page="page.page"
        :page-size="page.rows" 
        :total="page.total"
      />
    </div>
    <el-dialog
      v-show="addShow"
      :visible.sync="addShow"
      :title="form.title"
      width="54%"
      :before-close="formClose"
      center
      >
      <div class="detail-input">
        <el-form :inline="true" ref="form" :model="form" :rules="this.form.title === '' ? rules : {}" size="small" label-width="110px">
            <div>
                <el-form-item label="" prop="channelCode">
                    <el-input v-model="form.channelCode" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
                <el-form-item label="" prop="channelName">
                    <el-input v-model="form.channelName" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="" prop="channelType">
                    <el-select v-model="form.channelType" placeholder="" :disabled="form.disabled">
                        <el-option v-for="(item, index) in channelTypeList" :key="index" :label="item.dicName" :value="item.dicCode"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="productCode">
                    <el-select v-model="form.productCode" placeholder="" :disabled="form.disabled">
                        <el-option v-for="(item, index) in productCodeList" :key="index" :label="item.dicName" :value="item.dicCode"></el-option>
                    </el-select>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="" prop="gatewayGroup">
                    <el-input v-model="form.gatewayGroup" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
                <el-form-item label="" prop="channelMerId">
                    <el-input v-model="form.channelMerId" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="" prop="signType">
                    <el-select v-model="form.signType" placeholder="" :disabled="form.disabled">
                        <el-option label="RSA" value="RSA"></el-option>
                        <el-option label="MD5" value="MD5"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="certPath">
                    <el-input v-model="form.certPath" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="" prop="secretKey">
                    <el-input v-model="form.secretKey" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
                <el-form-item label="" prop="channelPubKey">
                    <el-input v-model="form.channelPubKey" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="" prop="pubKey">
                    <el-input v-model="form.pubKey" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
                <el-form-item label="" prop="prvKey">
                    <el-input v-model="form.prvKey" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="appid" prop="channelAppid">
                    <el-input v-model="form.channelAppid" placeholder="appid" :disabled="form.disabled"></el-input>
                </el-form-item>
                <el-form-item label="" prop="rate">
                    <el-input v-model="form.rate" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="">
                    <el-input v-model="form.sort" placeholder="" :disabled="form.disabled"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="form.status" placeholder="" :disabled="form.disabled">
                        <el-option label="" value="VALID"></el-option>
                        <el-option label="" value="INVALID"></el-option>
                    </el-select>
                </el-form-item>
            </div>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="formClose"> </el-button>
        <el-button size="small" type="primary" @click="formSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import { axiosMixin, listMixin } from "static/js/mixin.js";
import _router from "service/router-service.js";
import _common from "service/common-service.js";
export default {
  mixins: [axiosMixin, listMixin],
  data() {
    return {
      searchForm: {},
      searchRules: {},
      form: {},
      rules: {
        channelCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        channelName: [
          { required: true, message: "", trigger: "blur" }
        ],
        channelType: [
          { required: true, message: "", trigger: "blur" }
        ],
        productCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        gatewayGroup: [
          { required: true, message: "", trigger: "blur" }
        ],
        channelMerId: [
          { required: true, message: "", trigger: "blur" }
        ],
        signType: [
          { required: true, message: "", trigger: "blur" }
        ],
        certPath: [
          { required: true, message: "", trigger: "blur" }
        ],
        secretKey: [{ required: true, message: "", trigger: "blur" }],
        channelPubKey: [
          { required: true, message: "", trigger: "blur" }
        ],
        pubKey: [
          { required: true, message: "", trigger: "blur" }
        ],
        prvKey: [
          { required: true, message: "", trigger: "blur" }
        ],
        channelAppid: [
          { required: true, message: "appid", trigger: "blur" }
        ],
        rate: [{ required: true, message: "", trigger: "blur" }],
        sort: [{ required: true, message: "", trigger: "blur" }],
        status: [{ required: true, message: "", trigger: "blur" }]
      },
      productCodeList: {},
      channelTypeList: {}
    };
  },
  created() {
    this._renderTableDate();
    this.getProductCodeList();
    this.getChannelTypeList();
  },
  methods: {
    // 
    _renderTableDate(data) {
      const _this = this;
      _this.loading = true;
      _router.getChannelList(data).then(res => {
        _this.renderTableDate(res, (item, index) => {
          return {
            no: index + 1,
            channelId: item.channelId,
            channelCode: item.channelCode,
            channelName: item.channelName,
            channelType: item.channelType,
            channelTypeDesc: item.channelTypeDesc,
            productCode: item.productCode,
            productTypeDesc: item.productTypeDesc,
            gatewayGroup: item.gatewayGroup,
            sort: item.sort,
            status: item.status === "VALID" ? "" : "",
            createTime: item.createTime,
            updateTime: item.modifiedTime,
            operate: {
              update: true
            }
          };
        });
      });
    },
    // 
    getProductCodeList() {
      const _this = this;
      _common.getDictionaryList("PRODUCT_CODE").then(res => {
        _this.filterAxios(res, res => {
          _this.productCodeList = res;
        });
      });
    },
    // 
    getChannelTypeList() {
      const _this = this;
      _common.getDictionaryList("CHANNEL_TYPE").then(res => {
        _this.filterAxios(res, res => {
          _this.channelTypeList = res;
        });
      });
    },
    // 
    searchSubmit(searchForm) {
      const _this = this;
      this.$refs[searchForm].validate(valid => {
        if (valid) {
          _this._renderTableDate(_this.$refs[searchForm].model);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //  
    handleForm(row, type) {
      const _this = this;
      if (row instanceof Object) {
        _this.getChannelDetail(row.channelId, type);
      } else {
        _this.form = {
          title: ""
        };
      }
      _this.addShow = true;
    },
    // 
    getChannelDetail(channelId, type) {
      const _this = this;
      _router.getChannelDetail(channelId).then(res => {
        _this.filterAxios(res, res => {
          _this.form = {
            channelCode: res.channelCode,
            channelName: res.channelName,
            channelType: res.channelType,
            productCode: res.productCode,
            gatewayGroup: res.gatewayGroup,
            channelMerId: res.channelMerId,
            signType: res.signType,
            certPath: res.certPath,
            secretKey: res.secretKey,
            channelPubKey: res.channelPubKey,
            pubKey: res.pubKey,
            prvKey: res.prvKey,
            channelAppid: res.channelAppid,
            rate: res.rate,
            sort: res.sort,
            status: res.status
          };
          if (type === "") {
            _this.form.title = "";
            _this.form.disabled = false;
          } else if (type === "") {
            _this.form.title = "";
            _this.form.disabled = true;
          }
        });
      });
    },
    formClose() {
      this.addShow = false;
      this.form = {};
      this.$refs["form"] && this.$refs["form"].resetFields();
    },
    formSubmit() {
      const _this = this;
      this.$refs.form.validate(valid => {
        if (valid) {
          delete _this.form.disabled; // 
          if (_this.form.title === "") {
            delete _this.form.title;
            _router.addChannel(_this.form).then(res => {
              _this.formatResult(res, "");
            });
          } else {
            delete _this.form.title;
            _router.updateChannel(_this.form).then(res => {
              _this.formatResult(res, "");
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    formatResult(res, msg) {
      const _this = this;
      _this.formClose();
      _this.filterAxios(res, res => {
        _this.successTips(msg);
        _this._renderTableDate();
      });
    },
    // 
    handleSupportBankList(channelId, channelName) {
      console.log("handleSupportBankList!", channelId);
      this.$router.push({
        path: "/channel_support_bank",
        query: {
          channelId: channelId,
          channelName: channelName
        }
      });
    }
  }
};
</script>


