<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
    </div>
    <div class="operating">
        <el-form :inline="true" :model="searchForm" ref="searchForm" class="demo-form-inline" size="mini">
            <el-form-item label="ID" prop="appId">
                <el-input v-model="searchForm.appId"></el-input>
            </el-form-item>
            <el-form-item label="" prop="merchantId">
                <el-input v-model="searchForm.merchantId"></el-input>
            </el-form-item>
            <el-form-item label="" prop="status">
                <el-select v-model="searchForm.status" placeholder="">
                    <el-option label="" value="VALID"></el-option>
                    <el-option label="" value="INVALID"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="searchSubmit('searchForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
    <div class="table">
      <el-table
        v-loading="loading"
        :data="tableData"
        size="small"
        border
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column align="center" prop="no" label="" width="100" fixed></el-table-column>
        <el-table-column align="center" prop="appId" label="ID" fixed></el-table-column>
        <el-table-column align="center" prop="merchantId" label=""></el-table-column>
        <el-table-column align="center" prop="md5Key" label="md5" width="150"></el-table-column>
        <el-table-column align="center" prop="status" label=""></el-table-column>
        <el-table-column align="center" prop="createTime" label="" width="160"></el-table-column>
        <el-table-column align="center" prop="updateTime" label="" width="160"></el-table-column>
        <el-table-column align="center" prop="operate" label="" width="180" fixed="right">
          <template slot-scope="scope">
            <el-button v-if="scope.row.operate.update" @click="handleForm(scope.row)" type="warning" size="mini"></el-button>
            <el-button @click="handelInterface(scope.row)" type="success" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination background layout="prev, pager, next" 
        v-if="page.totalPages > 1"
        @current-change="handleCurrentChange"
        :current-page="page.page"
        :page-size="page.rows" 
        :total="page.total"
      />
    </div>
     <el-dialog
      v-show="updateShow"
      :visible.sync="updateShow"
      title=""
      width="30%"
      :before-close="updateClose"
      center
      >
      <div>
        <el-form ref="updateForm" :model="updateForm" :rules="updateRules" size="small" label-width="105px">
          <el-form-item label="id">
            <el-input v-model="updateForm.appId" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="updateForm.merchantId" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="md5" prop="md5Key">
            <el-input v-model="updateForm.md5Key" placeholder="md5"></el-input>
          </el-form-item>
          <el-form-item label="">
              <el-select v-model="updateForm.status" placeholder="">
                  <el-option label="" value="VALID"></el-option>
                  <el-option label="" value="INVALID"></el-option>
              </el-select>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="updateShow = false"> </el-button>
        <el-button size="small" type="primary" @click="updateSubmit()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import { axiosMixin, listMixin, validMixin } from "static/js/mixin.js";
import _app from "service/app-service.js";
export default {
  mixins: [axiosMixin, listMixin, validMixin],
  data() {
    return {
      searchForm: {
        appId: "",
        merchantId: "",
        status: ""
      },
      updateRules: {
        md5Key: [{ required: true, message: "md5", trigger: "blur" }],
        status: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  created() {
    this._renderTableDate();
  },
  methods: {
    // 
    _renderTableDate(data) {
      const _this = this;
      _this.loading = true;
      _app.getList(data).then(res => {
        _this.renderTableDate(res, (item, index) => {
          return {
            no: index + 1,
            id: item.id,
            appId: item.appId,
            merchantId: item.merchantId,
            md5Key: item.md5Key,
            status: item.status === "VALID" ? "" : "",
            createTime: item.createTime,
            updateTime: item.modifiedTime,
            operate: {
              update: true
            }
          };
        });
      });
    },
    // 
    searchSubmit(searchForm) {
      this._renderTableDate(this.searchForm);
    },
    // 
    handelInterface(row) {
      this.$router.push({
        path: "/interface_list",
        query: {
          merchantId: row.merchantId,
          appId: row.appId
        }
      });
    },
    // 
    handleForm(row) {
      this.updateShow = true;
      this.updateForm = {
        id: row.id,
        appId: row.appId,
        merchantId: row.merchantId,
        md5Key: row.md5Key,
        status: row.status === "" ? "VALID" : "INVALID"
      };
    },
    updateSubmit() {
      _app.updateApp(this.updateForm.id, this.updateForm).then(res => {
        this.filterAxios(res, res => {
          this.successTips("");
          this.updateClose();
          this._renderTableDate();
        });
      });
    }
  }
};
</script>


