<template>
  <div class="container">
    <div class="header clearfix">
      <h2></h2>
    </div>
    <div class="detail-input">
        <el-form :inline="true" :model="searchForm" ref="searchForm" class="demo-form-inline" size="mini" label-width="84px">
            <div class="row">
                <el-form-item label="">
                    <el-input v-model="searchForm.merchantId" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.merchantName" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.orderId" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.orderNo" placeholder=""></el-input>
                </el-form-item>
            </div>
            <div class="row">
                <el-form-item label="">
                    <el-date-picker
                    v-model="searchForm.tradeTime"
                    type="daterange"
                    align="right"
                    unlink-panels
                    range-separator=""
                    start-placeholder=""
                    end-placeholder=""
                    value-format="yyyy-MM-dd"
                    :picker-options="pickerOptions">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="searchForm.status" placeholder="">
                        <el-option value=""></el-option>
                        <el-option v-for="(item, index) in tradeStatusList" :key="index" :label="item.dicName" :value="item.dicCode"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="searchForm.tradeType" placeholder="">
                        <el-option value=""></el-option>
                        <el-option v-for="(item, index) in tradeTypeList" :key="index" :label="item.dicName" :value="item.dicCode"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="searchForm.productCode" placeholder="">
                        <el-option value=""></el-option>
                        <el-option v-for="(item, index) in productCodeList" :key="index" :label="item.dicName" :value="item.dicCode"></el-option>
                    </el-select>
                </el-form-item>
            </div>
            <div class="row">
                <el-form-item label="">
                    <el-input v-model="searchForm.channelCode" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.channelName" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.payeeName" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="searchForm.payerName" placeholder=""></el-input>
                </el-form-item>
            </div>
            <div class="row">
                <el-form-item label="">
                    <el-input v-model="searchForm.payerAccount" placeholder=""></el-input>
                </el-form-item>
                <el-form-item class="btn-item">
                    <el-button type="primary" @click="searchSubmit('searchForm')"></el-button>
                </el-form-item>
            </div>
        </el-form>
    </div>
    <div class="table">
      <el-table
        v-loading="loading"
        :data="tableData"
        size="small"
        border
        header-cell-class-name="table-th"
        style="width: 100%">
        <el-table-column align="center" prop="no" label="" fixed></el-table-column>
        <el-table-column align="center" prop="merchantId" label="" fixed width="120"></el-table-column>
        <el-table-column align="center" prop="merchantName" label="" fixed></el-table-column>
        <el-table-column align="center" prop="orderId" label="" fixed width="160"></el-table-column>
        <el-table-column align="center" prop="orderNo" label="" width="170"></el-table-column>
        <el-table-column align="center" prop="tradeTypeName" label=""></el-table-column>
        <el-table-column align="center" prop="productName" label=""></el-table-column>
        <el-table-column align="center" prop="channelName" label=""></el-table-column>
        <el-table-column align="center" prop="payAmount" label=""></el-table-column>
        <el-table-column align="center" prop="fee" label=""></el-table-column>
        <el-table-column align="center" prop="statusName" label=""></el-table-column>
        <el-table-column align="center" prop="payerName" label="" width="100"></el-table-column>
        <el-table-column align="center" prop="payeeName" label="" width="100"></el-table-column>
        <el-table-column align="center" prop="payerAccount" label="" width="100"></el-table-column>
        <el-table-column align="center" prop="createTime" label="" width="150"></el-table-column>
        <el-table-column align="center" prop="completeTime" label="" width="150"></el-table-column>
        <el-table-column align="center" prop="operate" label="" fixed="right">
          <template slot-scope="scope">
            <el-button @click="handleDetail(scope.row)" type="warning" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pagination">
      <el-pagination background layout="prev, pager, next" 
        v-if="page.totalPages > 1"
        @current-change="handleCurrentChange"
        :current-page="page.page"
        :page-size="page.rows" 
        :total="page.total"
      />
    </div>
    <el-dialog
      v-show="detailShow"
      :visible.sync="detailShow"
      title=""
      width="66%"
      :before-close="detailClose"
      center
      >
      <div class="detail-input">
        <el-form :inline="true" ref="detailForm" :model="detailForm" size="mini" label-width="105px">
            <div>
                <el-form-item label="">
                    <el-input v-model="detailForm.merchantId" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.merchantName" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.merchantRate" readonly></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="">
                    <el-input v-model="detailForm.channelCode" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.channelName" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.channelRate" readonly></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="">
                    <el-input v-model="detailForm.orderId" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.orderNo" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.sendChannelOrderNo" readonly></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="">
                    <el-input v-model="detailForm.tradeTypeName" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.productName" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.statusName" readonly></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="">
                    <el-input v-model="detailForm.totalAmount" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.payAmount" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.fee" readonly></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="">
                    <el-input v-model="detailForm.createTime" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.sendChannelTime" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.completeTime" readonly></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="">
                    <el-input v-model="detailForm.payeeName" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.payerName" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.payerAccount" readonly></el-input>
                </el-form-item>
            </div>
            <div>
                <el-form-item label="">
                    <el-input v-model="detailForm.profitName" readonly></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="detailForm.profitAmount" readonly></el-input>
                </el-form-item>
            </div>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="detailShow = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script type="text/ecmascript-6">
import { axiosMixin, listMixin } from "static/js/mixin.js";
import _trade from "service/trade-service.js";
import _common from "service/common-service.js";
export default {
  mixins: [axiosMixin, listMixin],
  data() {
    return {
      detailShow: false,
      detailForm: {},
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      searchForm: {},
      tableData: [],
      productCodeList: [],
      tradeStatusList: [],
      tradeTypeList: []
    };
  },
  created() {
    this._renderTableDate();
    this.getProductCodeList();
    this.getTradeStatusList();
    this.getTradeTypeList();
  },
  methods: {
    // 
    _renderTableDate(data) {
      this.loading = true;
      _trade.getList(data).then(res => {
        this.renderTableDate(res, (item, index) => {
          return {
            no: index + 1,
            merchantId: item.merchantId,
            merchantName: item.merchantName,
            orderId: item.orderId,
            orderNo: item.orderNo,
            tradeTypeName: item.tradeTypeName,
            productName: item.productName,
            channelName: item.channelName,
            payAmount: item.payAmount,
            fee: item.fee,
            statusName: item.statusName,
            payerName: item.payerName,
            payeeName: item.payeeName,
            payerAccount: item.payerAccount,
            createTime: item.createTime,
            completeTime: item.completeTime
          };
        });
      });
    },
    // 
    getProductCodeList() {
      _common.getDictionaryList("PRODUCT_CODE").then(res => {
        this.filterAxios(res, res => {
          this.productCodeList = res;
        });
      });
    },
    // 
    getTradeStatusList() {
      _common.getDictionaryList("TRADE_STATUS").then(res => {
        this.filterAxios(res, res => {
          this.tradeStatusList = res;
        });
      });
    },
    // 
    getTradeTypeList() {
      _common.getDictionaryList("TRADE_TYPE").then(res => {
        this.filterAxios(res, res => {
          this.tradeTypeList = res;
        });
      });
    },
    // 
    searchSubmit() {
      if (this.searchForm.tradeTime && this.searchForm.tradeTime.length === 2) {
        this.searchForm.createTimeStart =
          this.searchForm.tradeTime[0] + " 00:00:00";
        this.searchForm.createTimeEnd =
          this.searchForm.tradeTime[1] + " 00:00:00";
      }
      this._renderTableDate(this.searchForm);
    },
    detailClose() {
      this.detailShow = false;
      this.$refs["detailForm"].resetFields();
    },
    handleDetail(row) {
      _trade.getDetail(row.orderId).then(res => {
        this.filterAxios(res, res => {
          this.detailForm = res;
          console.log(res);
        });
      });
      this.detailShow = true;
    }
  }
};
</script>
<style lang="scss" scoped>
.row {
  display: flex;
  .el-form-item {
    flex: 1;
  }
}
</style>



